@import '~node_modules/modern-normalize/modern-normalize.css';

@font-face {
  font-family: 'kumbh_sansregular';
  src: url('../font/400/kumbhsans-variablefont_yopqwght-webfont.eot');
  src: url('../font/400/kumbhsans-variablefont_yopqwght-webfont.eot?#iefix')
      format('embedded-opentype'),
    url('../font/400/kumbhsans-variablefont_yopqwght-webfont.woff2')
      format('woff2'),
    url('../font/400/kumbhsans-variablefont_yopqwght-webfont.woff')
      format('woff'),
    url('../font/400/kumbhsans-variablefont_yopqwght-webfont.ttf')
      format('truetype'),
    url('../font/400/kumbhsans-variablefont_yopqwght-webfont.svg#kumbh_sansregular')
      format('svg');
  font-weight: normal;
  font-style: normal;
}

@font-face {
  font-family: 'kumbh_sansblack';
  src: url('../font/900/kumbhsans-black-webfont.eot');
  src: url('../font/900/kumbhsans-black-webfont.eot?#iefix')
      format('embedded-opentype'),
    url('../font/900/kumbhsans-black-webfont.woff2') format('woff2'),
    url('../font/900/kumbhsans-black-webfont.woff') format('woff'),
    url('../font/900/kumbhsans-black-webfont.ttf') format('truetype'),
    url('../font/900/kumbhsans-black-webfont.svg#kumbh_sansblack') format('svg');
  font-weight: normal;
  font-style: normal;
}

html,
body,
div,
span,
applet,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
abbr,
acronym,
address,
big,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
s,
samp,
small,
strike,
strong,
sub,
sup,
tt,
var,
b,
u,
i,
center,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
canvas,
details,
embed,
figure,
figcaption,
footer,
header,
hgroup,
menu,
nav,
output,
ruby,
section,
summary,
time,
mark,
audio,
video,
button {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
}
/* HTML5 display-role reset for older browsers */
article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {
  display: block;
}
body {
  line-height: 1;
}
ol,
ul {
  list-style: none;
}
blockquote,
q {
  quotes: none;
}
blockquote:before,
blockquote:after,
q:before,
q:after {
  content: '';
  content: none;
}
table {
  border-collapse: collapse;
  border-spacing: 0;
}
